.App-header {
  background-color: black;
  min-height: 4vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding:0px 20px;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  max-width: 550px; /* optional: set a max width for the container */
  margin-bottom: -40px;
}

.container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container:before {
  content: "";
  display: block;
  padding-bottom: 50%; /* set padding-bottom to match aspect ratio */
}


